
import { mapState, mapGetters } from 'vuex';
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Layout from './components/Layout'

export default {
components: {
ViewBox,
Layout,
XHeader,
},

computed: {
...mapState({
locale: state => state.i18n.locale,
}),
...mapGetters([
"title",
"direction",
'rightOption',
'leftOption'
]),
back: {
get: function () {
return this.$t('Back')
}
}
},

watch: {
locale: 'setTitle',
},

methods: {
setTitle() {
document.title = this.$t('vue-leaflet-mobile')
}
},

created() {


},

mounted() {
},
}




